.row {
  margin-left: 20px;
  color: white;
}
.row__posters {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: 20px;
}

.row__posters::-webkit-scrollbar {
  background-color: grey;
  width: 5px;
  height: 2.5px;
}

.row__posters::-webkit-scrollbar-thumb {
  background-color: darkgrey;
  outline: 1px solid slategrey;
  border-radius: 10px;
}

.row__posters::-webkit-scrollbar-thumb:hover {
  background-color: grey;
  cursor: pointer;
}

.row__posters::-webkit-scrollbar-track {
  box-shadow: inset 0 0 6px rgba(0, 0, 0, 1);
  border-radius: 10px;
}

/*
.row__posters::-webkit-scrollbar-track-piece {
  box-shadow: inset 0 0 6px rgba(0, 255, 255, 1);
}*/
.row__poster {
  object-fit: contain;
  width: 100%;
  max-height: 100px;
  transition: transform 450ms;
  margin-right: 10px;
}

.row__poster:hover {
  transform: scale(1.08);
  cursor: pointer;
}

.row__posterLarge {
  max-height: 250px;
}

.row__posterLarge:hover {
  transform: scale(1.09);
}
