.footer {
  color: #e6e6e6;
  height: 300px;
}

.footer__content {
  padding-top: 50px;
  max-width: 980px;
  margin: 20px auto 0;

  font-size: 14px;
  font-weight: 200;
  padding: 0 4%;
}

.footer__list {
  display: flex;
  justify-content: space-around;
  flex-wrap: wrap;
  flex-direction: row;

  align-items: flex-start;
  padding: 10px;

  font-size: 13px;
}

.footer__list__wrapper {
  flex: 0 0 20%;
  margin-bottom: 16px;
  flex-basis: 25%;
  box-sizing: border-box;
}

.footer__list li {
  margin-top: 15px;
  list-style-type: none;
}

.footer__list li:hover {
  border-bottom: 1px solid grey;
  cursor: pointer;
}

.footer__button {
  margin-top: 20px;
}

.footer__button button {
  background-color: transparent;
  outline: none;
  border: none;
  color: grey;
  border: 1px solid rgb(151, 151, 151);
  padding: 7px;
  text-align: center;
  word-spacing: normal;
  letter-spacing: normal;
}
.footer__button button:hover {
  color: white;
  cursor: pointer;
}

.footer__copyright {
  color: grey;
  font-size: 13px;
  text-align: center;
}

.footer__copyright h5 {
  margin: 5px;
}
